@import '~antd/dist/antd.css';
.m-login-wrap{display: flex; position: absolute;top: 0;left: 0;right: 0;bottom: 0;background: url(../images/m-login-bg.jpg) no-repeat center;}
.m-login{margin: auto;padding: 50px 0 0; width: 400px;height: 300px;border: 1px solid #ddd;}
.m-login-row{display: flex; margin: 20px;}
.m-login-icon{width: 40px;font-size: 26px;}
.m-login-btn-wrap{margin: 20px; padding: 0 0 0 36px;}
.m-loading{display: none; position: absolute;top: 0;left: 0;right: 0;bottom: 0;background: rgba(0, 0, 0, 0.2);}
.m-loading::before{margin: auto; content: '';display: inline-block;width: 38px;height: 38px;background-image: url(../images/loading.png);animation: loading 0.5s linear infinite;}
.m-loading.active{display: flex;}
.m-index-wrap{display: flex; position: absolute;top: 0;left: 0;right: 0;bottom: 0;}
.m-list{position: relative;flex:1}
.m-sidebar{overflow-y: auto;}
.m-sidebar-icon{margin: 0 3px; font-size: 18px;vertical-align: top;}






















@keyframes loading {
  0% {transform: rotate(0deg);}
  100% { transform: rotate(360deg);}
}